<template lang="html">
  <div class="bad-comment-detail-page">
    <!-- 头部 -->
    <default-head header="待评价" path="/personcenter"></default-head>
    <light-title text="售后待评价明细"></light-title>

    <waiting-comment-list v-for="(item, index) in waitComment" :key="index" :data="item"></waiting-comment-list>
    <div class="comment-reward">
      <p class="comment-reward__warn redColor">为了您以后能找到更准确的信息，请对卖家进行客观的售后评价</p>
      <p class="comment-reward__title">评价后奖励</p>
      <white-title class="borderB" text="评价后您将获得随机数量的“召币”"></white-title>
      <white-title text="评价后您将有权限查看该卖家的“素颜照”"></white-title>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import lightTitle from '@/components/common/lightTitle'
import whiteTitle from '@/components/common/whiteTitle'
import waitingCommentList from '@/components/waitingComment/waitingCommentList'

export default {
  data () {
    return {
      loading: false,
      pageSize: 100,
      pageIndex: 1,
      pages: 1,
      waitComment: []
    }
  },
  computed: {
    userId: function () {
      return utils.userId()
    }
  },
  mounted () {
    this.getCommentList(this.userId, 0, this.pageIndex++, this.pageSize)
  },
  methods: {
    getCommentList (userId, status, pageIndex, pageSize) {
      minutils.IndicatorOpen('加载中')
      // 接口参数变化
      api.getCommentList({userId: userId, status: status, pageIndex: pageIndex, pageSize: pageSize}).then((response) => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.pages = result.data.total
          this.waitComment = [...this.waitComment, ...result.data.list]
        }
      })
    }
  },
  components: { defaultHead, lightTitle, whiteTitle, waitingCommentList }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.bad-comment-detail-page {
  padding-top: .88rem;
  .comment-reward {
    .comment-reward__warn {
      padding: 0 .22rem;
      font-size: .24rem;
      line-height: .44rem;
      padding-top: .1rem;
      font-style: italic;
    }
    .comment-reward__title {
      padding: 0 .22rem;
      font-size: .32rem;
      line-height: .72rem;
    }
  }
}

</style>



