<template lang="html">

  <div class="search-list clearfix" >
    <div class="search-list-item">
      <!-- 左侧 -->
      <div class="search-list-left" :class="{'woman': parseInt(data.sex) === 2}">
        <img class="search-list-left__img" v-if="data.userType" :src="data.logo || zhanwt">
        <img v-else-if="parseInt(data.userType) === 0 && parseInt(data.sex) === 0" :src="this.defaultLogo" alt="">
        <span class="search-list-left__name" v-else>{{data.realName | toFormatName}}</span>
        <!-- <span class="search-list-left__name" v-else-if="!data.nickName && !data.enterpriseId">{{data.userId}}</span> -->
        <span class="search-list-left__vip" :class="{'redColor': !!data.isVip, 'borderBRed': !!data.isVip}">VIP</span>
      </div>
      <!-- 中间灰线 -->
      <span class="search-list-line"></span>
      <!-- 右侧 -->
      <div class="search-list-right">
        <div class="search-list-right__top clearfix">
          <span class="search-list-right__number fl">{{data.mobile | toFormatMobile}}</span>
        </div>
        <div class="search-list-right__middle">
          <span class="search-list-right__address" v-if="data.mobilePlace">{{data.mobilePlace}}</span>
          <span class="search-list-right__address" v-else>{{data.province}}{{data.city}}</span>
          <i class="arrow__icon"  v-if="isShow"></i>
        </div>
        <div class="search-list-right__bottom clearfix">
          <span class="search-list-right__info fl">{{data.tags}}</span>
        </div>
        <div class="search-list-right__button clearfix">
          <span class="button__comment fl activebgColor" @click="completeComment(data)">完成交易<br>去评价</span>
          <span class="button__comment fl redbgColor" v-if="false">未交易<br>不评价</span>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import hzl from '@/assets/img/hzl@2x.png'
import logo from '@/assets/img/logo.png'
export default {
  props: {
    data: Object,
    isShow: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      zhanwt: hzl,
      defaultLogo: logo
    }
  },
  methods: {
    getListDetail (item) {
      // console.log(item)
      this.$router.push('/detail')
    },
    completeComment (item) {
      // console.log(item)
      sessionStorage.setItem('searchId', item.userId)
      sessionStorage.setItem('commentId', item.id)
      this.$router.push('/completecomment')
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.search-list-item {
  width: 100%;
  height: 1.56rem;
  padding: .2rem 0 .2rem .22rem;
  background: @whiteColor;
  border-bottom: 1px solid @lineColor;

  .search-list-left {
    width: .8rem;
    height: 100%;
    float: left;
    .search-list-left__name {
      display: block;
      text-align: center;
      width: .6rem;
      height: .6rem;
      font-size: .32rem;
      line-height: .6rem;
      color: @whiteColor;
      background: @manColor;
      border-radius: 50%;
    }
    .search-list-left__img {
      display: block;
      width: .6rem;
      height: .6rem;
      background: transparent;
      border-radius: 50%;
    }
    .search-list-left__vip {
      display: block;
      width: .6rem;
      text-align: center;
      padding-bottom: .04rem;
      margin-top: .14rem;

      font-size: .3rem;
      font-style: italic;
      color: @lightColor;
      border-bottom: 1px solid @lightColor;
    }
    .borderBRed {
      border-bottom: 1px solid @redColor !important;
    }
    &.woman {
      .search-list-left__name {
        background: @womanColor !important;
      }
    }
  }
  .search-list-line {
    float: left;
    width: 1px;
    height: .94rem;
    margin-top: .11rem;
    background: @lineColor;
  }
  .search-list-right {
    margin-left: 1rem;
    position: relative;
    .search-list-right__top {
      font-size: .28rem;
      color: @defaultColor;
      height: .24rem;
      line-height: .24rem;
      .search-list-right__number {
        font-size: .32rem;
        position: relative;
        // padding-left: .32rem;
        img {
          width: .44rem;
          height: .44rem;
          position: absolute;
          left: -.12rem;
          top: -.1rem;
        }
      }
    }
    .search-list-right__middle {
      width: 100%;
      font-size: .28rem;
      position: relative;
      height: .64rem;
      line-height: .64rem;
      .search-list-right__address {
        color: @lightColor;
      }
      .arrow__icon {
        right: .08rem;
        top: .1rem;
      }
    }
    .search-list-right__bottom {
      width: 100%;
      font-size: .28rem;
      padding-right: .42rem;
      height: .28rem;
      line-height: .28rem;
      .search-list-right__info {
        display: inline-block;
        height: .3rem;
        line-height: .32rem;
        max-width:4rem;
        overflow: hidden;
        text-overflow: ellipsis;
        color: @infoColor;
      }
    }
    .search-list-right__button {
      position: absolute;
      right: .8rem;
      top: 0;
      .button__comment {
        width: 1.3rem;
        height: 1.2rem;
        line-height: .46rem;
        padding-top: .14rem;
        font-size: .28rem;
        text-align: center;
        color: @whiteColor;
        border-radius: .1rem;
        &:last-child {
          margin-left: .2rem;
        }
      }
    }
  }
}
</style>



